.blocker {
    position: fixed;
    top: 0; right: 0; bottom: 0; left: 0;
    width: 100%; height: 100%;
    overflow: auto;
    z-index: 1;
    padding: 20px;
    box-sizing: border-box;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.75);
    text-align: center;
}
.blocker:before{
    content: "";
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    margin-right: -0.05em;
}
.blocker.behind {
    background-color: transparent;
}
.modal {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    z-index: 2;
    box-sizing: border-box;

    // Sizing
    width: 90%;

    &.small {
        max-width: 500px;
    }
    // &.small {
    //     max-width: 500px;
    // }

    padding: 58px 2.8rem 1rem 2.8rem;
    border-radius: 0px;
    box-shadow: $shadow;
    text-align: left;

    background: color(white);
    border-top-color: color(primary);
    border-top-width: 3px;
    border-top-style: solid;
}

.modal a.close-modal {
    position: absolute;
    top: 1.5rem;
    right: 0.5rem;

    display: block;
    width: 30px;
    height: 30px;
    // text-indent: -9999px;
    // background: url("data:image/png;base64,") no-repeat 0 0;
}

.modal-spinner {
    display: none;
    width: 64px;
    height: 64px;
    position: fixed;
    top: 50%;
    left: 50%;
    margin-right: -32px;
    margin-top: -32px;
    background: url("data:image/gif;base64,") #111 no-repeat center center;
    border-radius: 8px;
}
